/**
 * 通用工具
 * 
 */

import axios from '@/vuex/http'
import store from '@/vuex/store'
import router from '../router'
import * as types from '@/vuex/types'

var utils = {
  // 按类型查字典列表
  initDict (type, dictlist, callback) {
    axios.get('/sys/dict/list?type=' + type)
      .then(res => {
        if (res && res.code === '0') {
          res.data.forEach(dict => dictlist.push(dict))
          if (callback) callback()
        }
      }).catch(err => {
        console.warn(err)
      })
  },
  // 按类型查字典标签
  getDictLabel (type, value, callback) {
    axios.get('/sys/dict/list?type=' + type)
      .then(res => {
        if (res && res.code === '0') {
          let dict = res.data.find(e => e.value === value)
          if (callback) callback(dict.label)
        }
      }).catch(err => {
        console.warn(err)
      })
  },
  // 从字典列表中取字典标签
  findDictLabel: function (dictlist, value) {
    let d = dictlist.find((dict) => dict.value === value)
    if (d) return d.label
    return null
  },
  // 构建树菜单
  buildTreeMenu: function (list, expand) {
    return this.buildTree(list, 'pId', expand)
  },
  // 构建树形表
  buildTreeData: function (list) {
    return this.buildTree(list, 'parentId', false)
  },
  // 构建简单树
  buildTree: function (list, pidname, expand) {
    let ret = []
    if (list.length === 0) return ret
    list.forEach(e => {
      let mark = false
      for (var item of list) {
        if (e[pidname] === item.id) {
          mark = true
          if (!item.children) item.children = []
          item.children.push(e)
          item.expand = expand
          break
        }
      }
      if (!mark) ret.push(e)
    })
    return ret
  },
  // 获取所有上级节点列表
  getParentList: function (list, pidname, id) {
    let p = list.find(e => e.id === id)
    if (!p || !p[pidname]) return null
    let pid = p[pidname]
    let result = []
    while (true) {
      let parent = list.find(e => e.id === pid)
      if (parent) {
        result.push(parent)
        pid = parent[pidname]
      } else break
    }
    return result
  },
  // UUID
  UUID (type, dictlist, callback) {
    let d = new Date().getTime()
    let uuid = 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, c => {
      let r = (d + Math.random() * 16) % 16 | 0
      d = Math.floor(d / 16)
      return (c === 'x' ? r : (r & 0x3 | 0x8)).toString(16)
    })
    return uuid
  },
  // 浮点数的格式化
  formatFloat (f, digit) {
    var m = Math.pow(10, digit)
    return parseInt(f * m, 10) / m
  },
  getSetting (key) {
    let settings = localStorage.getItem('settings')
    if (settings === null) {
      store.commit(types.SETTINGS, JSON.stringify({}))
      return null
    } else {
      settings = JSON.parse(settings)
      return settings[key]
    }
  },
  setSetting (key, value) {
    let settings = localStorage.getItem('settings')
    if (settings === null) {
      settings = {key: value}
    } else {
      settings = JSON.parse(settings)
      settings[key] = value
    }
    store.commit(types.SETTINGS, JSON.stringify(settings))
  },
  go (url) {
    router.push({path: url})
  },
  hasPermission (permission) {
    if (store.state.user.admin) return true
    let p = store.state.permissions.find(e => e === permission)
    return p === permission
  }
}
export default utils



