<template>
  <div class="app flex-row align-items-center">
    <div class="container">
      <div class="row justify-content-center">
        <div class="col-md-6">
          <div class="clearfix">
            <h1 class="float-left display-3 mr-4">404</h1>
            <h4 class="pt-3">错误!</h4>
            <p class="text-muted">你要访问的页面不存在.</p>
          </div>
          <div class="input-prepend input-group">
            <router-link class="mr-4" :to="'/dashboard'"><i class="fa fa-home"></i> 首页</router-link>
            <router-link :to="'/dashboard'"><i class="fa fa-arrow-left"></i> 返回上页</router-link>
          </div>
          <div class="input-prepend input-group">
            <span class="input-group-addon"><i class="fa fa-search"></i></span>
            <input id="prependedInput" class="form-control" size="16" type="text" placeholder="输入你要查找的内容">
            <span class="input-group-btn">
              <button class="btn btn-info" type="button">查找</button>
            </span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Page404'
}
</script>



