/**
 * http配置
 * 
 */

import env from './env'
import axios from 'axios'
import store from './store'
import * as types from './types'
import router from '../router'
import iView from 'iview'

// axios 配置
axios.defaults.timeout = 30000
axios.defaults.baseURL = env.apiUrl
axios.defaults.headers.common['Authorization'] = ''
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded'

// http request 拦截器
axios.interceptors.request.use(
  config => {
    iView.LoadingBar.start()
    let token = localStorage.getItem('token')
    if (token) {
      config.headers.Authorization = `token ${token}`
    }
    return config
  },
  err => {
    console.error(err)
    iView.LoadingBar.error()
    iView.Message.error('请求出错')
  })

// http response 拦截器
axios.interceptors.response.use(
  response => {
    if (!response.data) response.data = {code: '999', message: '服务器出现错误'}
    if (response.data.code !== '0') {
      iView.LoadingBar.error()
    } else iView.LoadingBar.finish()
    return response.data
  },
  error => {
    iView.LoadingBar.error()
    if (error.response) {
      console.log(error.response)
      switch (error.response.status) {
        case 0:
          iView.Message.error('请求响应错误，请检查网络')
          break
        case 400:
          let msg = '服务器出现错误'
          let data = error.response.data
          if (data !== null) msg = data.message + '[' + data.code + ']'
          iView.Notice.error({title: '服务器错误', desc: msg})
          break
        case 401:
          // 401 清除token信息并跳转到登录页面
          store.commit(types.LOGOUT)
          router.replace({
            path: '/auth/login',
            query: {redirect: router.currentRoute.query.redirect || router.currentRoute.fullPath}
          })
          break
        case 403:
          iView.Message.warning('没有访问权限，请联系管理员')
          break
        case 404:
          router.push({path: '/error/404'})
          break
        case 500:
          router.push({path: '/error/500'})
          break
        case 504:
          iView.Message.error('请求超时，请检查网络')
          break
        default:
          iView.Message.error('未知错误，请联系管理员')
      }
    } else {
      iView.Message.error('连接出现错误，请检查网络')
    }
    return Promise.reject(error)
  })

export default axios



