<template>
  <div>
    <AppHeader/>
      <main id="main">
        <div class="container">
          <router-view v-if="loaded"></router-view>
        </div>
      </main>
    <AppFooter/>
  </div>
</template>

<script>
import axios from '@/vuex/http'
import * as types from '@/vuex/types'
import { Header as AppHeader, Footer as AppFooter } from '../components/'
import utils from '@/vuex/utils'

export default {
  name: 'full',
  components: {
    AppHeader,
    AppFooter
  },
  data () {
    return {
      loaded: false,
      nav: []
    }
  },
  computed: {
    name () {
      return this.$route.name
    },
    list () {
      return this.$route.matched
    }
  },
  created: function () {
    let _this = this
    _this.getInfo(() => { _this.getMenuList() })
  },
  methods: {
    getInfo (callback) {
      let _this = this
      axios.get('/auth/user/info')
        .then(res => {
          _this.$store.commit(types.USER, res.data)
          callback()
        }).catch(err => {
          console.warn(err)
        })
    },
    getMenuList () {
      let _this = this
      axios.get('auth/menu/list')
        .then(res => {
          let menulist = []
          res.data.forEach(menu => {
            if (menu.id !== '1' && menu.type === '1') {
              menulist.push({id: menu.id, type: menu.type, name: menu.name, url: menu.href, icon: menu.icon, pId: menu.parentId, title: false})
            }
          })
          _this.nav = utils.buildTreeMenu(menulist, false)
          _this.getPermissionList()
        }).catch(err => {
          console.error(err)
        })
    },
    getPermissionList () {
      let _this = this
      axios.get('auth/user/permissions')
        .then(res => {
          let permissionlist = []
          res.data.forEach(menu => {
            if (menu.permission && menu.permission !== '') {
              permissionlist.push(menu.permission)
            }
          })
          _this.$store.commit(types.PERMISSIONS, permissionlist)
          _this.loaded = true
        }).catch(err => {
          console.error(err)
        })
    }
  }
}
</script>



