/**
 * 全局存储配置
 * 
 */

import Vue from 'vue'
import Vuex from 'vuex'
import * as types from './types'

Vue.use(Vuex)

const store = new Vuex.Store({
  // 定义状态
  state: {
    user: {},
    permissions: []
  },
  mutations: {
    [types.LOGIN]: (state, data) => {
      localStorage.setItem('token', data)
    },
    [types.LOGOUT]: (state) => {
      let notify = localStorage.getItem(types.SETTINGS)
      localStorage.clear()
      localStorage.setItem(types.SETTINGS, notify)
    },
    [types.USER]: (state, data) => {
      state.user = data
    },
    [types.PERMISSIONS]: (state, data) => {
      state.permissions = data
    },
    [types.SETTINGS]: (state, data) => {
      localStorage.setItem(types.SETTINGS, data)
    }
  }
})

export default store



