import Vue from 'vue'
import Router from 'vue-router'

// Containers
import Full from '@/containers/Full'

// Views
import Home from '@/views/Home'

// Views - common
import Page404 from '@/views/error/Page404'
import Page500 from '@/views/error/Page500'

Vue.use(Router)

export default new Router({
  mode: 'hash',
  linkActiveClass: 'open active',
  scrollBehavior: () => ({ y: 0 }),
  routes: [
    {
      path: '/',
      redirect: '/home',
      component: Full,
      children: [
        {
          path: 'home',
          name: '首页',
          component: Home
        }
      ]
    },
    {
      path: '/error',
      redirect: '/error/404',
      name: '错误',
      component: {
        render (c) { return c('router-view') }
      },
      children: [
        {
          path: '404',
          name: '404找不到',
          component: Page404
        },
        {
          path: '500',
          name: '500服务器错误',
          component: Page500
        }
      ]
    }
  ]
})



